/*Navigation*/

.navbar-toggler {
	@include media-breakpoint-up(lg) {
		display: none;
	}
	margin-top: 0.3rem;
	.navbar-toggler-icon {
		height: auto ;
	}
	.navbar-toggler-icon:before {
		content: '\f0c9';
	}
}

.navbar-brand {
	margin-left: 15px;
	@include media-breakpoint-up(lg) {
		display: inline-block;
		//margin-bottom: -60px;
	}
	a img {
		width: 100%;
	}
}

header nav.navbar {
	margin-right: -15px;
	margin-left: -15px;

	@include media-breakpoint-up(lg) {
		padding:0;
		display: block;
		box-shadow: none;
		margin-top: -84px;
	}
}

.menu-top-container {
	border-left: 1px solid $gray-dark;
	width: 35%;
	float: right;
	@include media-breakpoint-up(lg) {
		width: auto;
		border-left: none;
	}
}

.home .menu-top-container {
	border-left: 1px solid $gray;
	@include media-breakpoint-up(lg) {
		width: auto;
		border-left: none;
	}
}

.menu-primary-container {
	width: 60%;
	@include media-breakpoint-up(lg) {
		width: auto;
		float: right;
		margin-top: 0.5rem;
	}
}

.search-container {
	@include media-breakpoint-up(lg) {
		width: auto;
		float: right;
	}
}

.menu-top-container ul.navbar-nav {
	.nav-item {
		padding-left: $spacer/2;
		padding-right: $spacer/2;
	}

	.nav-item a {
		color: $blue;
		font-size: $font-size-sm;
		padding-top: 0.2rem;
		padding-bottom: 0.2rem;
		padding-left: 0;
		padding-right: 0;
	}

	.nav-item a:hover {
		color: $orange;
	}

	@include media-breakpoint-up(lg) {
		border-right: 1px solid lighten($gray-dark,20%);
		margin-right: 1.5rem;
		padding-right: 0.5rem;
	}

	li.lang-item-first {
		@include media-breakpoint-up(lg) {
			margin-left: 0.5rem;
			padding-left: 1rem;
			border-left: 1px solid lighten($gray-dark,20%);
		}
	}
}

.menu-primary-container ul.navbar-nav li.nav-item.current-page-ancestor,
.menu-primary-container ul.navbar-nav li.current_page_item {
	a.nav-link {
		/* color: $orange !important;	 */
	}
}

.home .menu-top-container ul.navbar-nav li.lang-item-first {
	@include media-breakpoint-up(lg) {
		border-left: 1px solid $gray;
	}
}
.home .menu-top-container ul.navbar-nav {
	@include media-breakpoint-up(lg) {
		border-right: 1px solid $gray;
	}
}

.menu-primary-container ul.navbar-nav {
	.nav-item a {
		color: $black;
		font-family: $font-family-serif;
		font-size: $font-size-lg*0.8;
		line-height: 1.1;
		margin-right: 0.5rem;
	}
	.nav-item a:hover {
		color: $orange;
	}

	li.nav-item.current_page_item a.nav-link,
	li.nav-item.current_page_parent a.nav-link,
	li.nav-item.current-page-ancestor a.nav-link{
		color: $orange;
	}
}

/* Submenu */
ul.navbar-nav {
	.dropdown-menu {
		white-space: normal;
		padding: 0.5rem 0;
		border: 0;
		box-shadow: none;
		border-radius: 0;
		background-color: transparent;

		.nav-item {
			padding-left: 0;
		}
		.nav-item a {
			font-family: $font-family-sans-serif;
			font-size: $font-size-sm;
			padding: 0.5rem 0rem;
			border-top: 1px solid $gray-dark;
			@include media-breakpoint-up(lg) {
				padding: 0.5rem 0.4rem;
				border-top: 0px;
			}
		}
		.nav-item a:hover {
			background-color: transparent;
		}
		min-width: 0;
		@include media-breakpoint-up(lg) {
			padding: .5rem .5rem;
			left: -.5rem;
			background-color: #ffffff;
			@include box-shadow($dropdown-box-shadow);
			min-width: 10rem;
		}
	}
}
.dropdown-toggle {
  &::after {
    display: inline-block;
    width: 0;
    height: 0;
    vertical-align: top;
    border: none;
    margin-left: 1px;
    content: '\f107';
  }
}

.clearfix-lg {
	@include media-breakpoint-up(lg) {
  	content: "";
  	display: table;
  	clear: both;
	}
}

.navigation.posts-navigation {
	margin-top: $spacer*3;
}

/*Footer Navi*/
footer.site-footer {
	padding-top: 4rem;
	padding-bottom: 8rem;
	ul li a {
		color: #fff;
		font-size: $font-size-lg*0.9;

		@include media-breakpoint-up(lg) {
			border-right: 1px solid white;
			padding: 0.2rem 1rem !important;
		}
	}

	ul li:last-child a{
		border-right: 0px;
	}
	ul li a:hover {
		text-decoration: underline !important;
	}
}
