/* Variables*/

$blue:    		#005b79;
$blue-light:    #d1e1e7;
$blue-dark:    	#00384a;

$gray:			#ebebeb;
$gray-dark:		#3c3c3c;

$indigo:  #6610f2 !default;
$purple:  #6f42c1 !default;
$pink:    #e83e8c !default;
$red:     #dc3545 !default;
$orange:  #ce492e;
$yellow:  #ffc107 !default;
$green:   #28a745 !default;
$teal:    #20c997 !default;
$cyan:    #17a2b8 !default;

$enable-rounded:            true;
$enable-shadows:            true;

$theme-colors: (
  primary: $blue,
  secondary: $orange,
  success: $green,
  info: $cyan,
  warning: $yellow,
  danger: $red,
  light: $gray,
  dark: $gray-dark
);

$spacer: 1rem;

$block-padding: $spacer*2;

// Links
$link-color:            $orange;
$link-decoration:       none !default;
$link-hover-color:      $blue;
$link-hover-decoration: underline !default;


// Fonts
$font-family-sans-serif: Roboto, Arial, sans-serif;
$font-family-serif: RobotoSlab, Arial, sans-serif;
$headings-font-family: $font-family-serif;

$h1-font-size: 2rem;
$h2-font-size: 1.75rem;
$h3-font-size: 1.50rem;
$h4-font-size: 1.20rem;
$h5-font-size: 1.1rem;
$h6-font-size: 1rem;

$headings-margin-bottom: ($spacer);
$headings-margin-top: ($spacer*2);

$headings-color: $blue;

$border-radius:          .2rem !default;
