/* Sidebar */

aside {
		ul.sidebar-nav {
			padding:0;
			list-style-type: none;
			li {
				padding: $spacer/3 0;
				a {
					color: #000;
				}
			}

			li.current_page_item a,
			li.current_page_parent a {
				color: $orange;
			}

			border-bottom: 3px solid $gray;
			padding-bottom: $spacer;

			ul.children {
				border-bottom: 0px;
				list-style-type: none;
				padding: 0;

				margin: $spacer/3 0;
				li {

					padding: $spacer/3 0 $spacer/3 $spacer;

					a {
						color: lighten($gray-dark,25%);
					}
				}
				li.current_page_item a {
					color: $orange;
				}
			}
		}
}
