/* A-Sit Plus Theme */

.navbar-brand {
	width: 120px  !important;
}

@media (min-width:992px) {
	.navbar-brand {
		width: 263px !important ;
	}
}

section.header-home {
	background: #cd492e url("../images/bg-header-home-asitplus.jpg") no-repeat center center  !important;
}

header.site-header {
	background: #ededed url("../images/bg-content-header-asitplus.jpg") repeat-x center bottom !important;
	border-bottom: 4px solid #cd492e !important;
}

.home header.site-header {
	background-image: none  !important;
	background-color: #ffffff !important;
	border-bottom: 0px !important;
}

.news-home {
	background: transparent url("../images/bg-news-home-asitplus.jpg") center top !important;
	background-color: #00202b !important;

	.block-article p{
		color: #ffffff;
	}

	.btn {
		color: #ffffff ;
		border-color: #ffffff ;
	}
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
	color:  #cd492e !important;
}

h2.entry-title a {
	color:  #cd492e !important;
}

article.block-article {
	h2 {
		a {
			color:  #cd492e !important;
		}
	}
}

footer.site-footer {
	background-color: $blue;
}

.unmute {
	opacity: 0.4;
}
.unmute:hover {
	opacity: 1;
}

.bg-head {
	background-image: url("../images/bg-alpha.webp");
	background-color: #f2f2f2;
	background-size: cover;
	background-position: center;
}

.page-index h2 {
	margin-top: 50px;
}
