@font-face {
	font-family: 'Roboto';
	src: url('../fonts/Roboto/Roboto.eot');
	src: url('../fonts/Roboto/Roboto.eot?#iefix') format('embedded-opentype'),
		 url('../fonts/Roboto/Roboto.woff2') format('woff2'),
		 url('../fonts/Roboto/Roboto.woff') format('woff'),
		 url('../fonts/Roboto/Roboto.ttf') format('truetype'),
		 url('../fonts/Roboto/Roboto.svg#Roboto_regularregular') format('svg');
	font-weight: 400;
	font-style: normal;
}
@font-face {
	font-family: 'Roboto';
	src: url('../fonts/Roboto/Roboto-Bold.eot');
	src: url('../fonts/Roboto/Roboto-Bold.eot?#iefix') format('embedded-opentype'),
		 url('../fonts/Roboto/Roboto-Bold.woff2') format('woff2'),
		 url('../fonts/Roboto/Roboto-Bold.woff') format('woff'),
		 url('../fonts/Roboto/Roboto-Bold.ttf') format('truetype'),
		 url('../fonts/Roboto/Roboto-Bold.svg#Robotobold') format('svg');
	font-weight: 700;
	font-style: bold;
}
