		 url('../fonts/RobotoSlab/RobotoSlab-Regular.ttf') format('truetype'),
		 url('../fonts/RobotoSlab/RobotoSlab-Regular.svg#Robotoslab') format('svg');
	font-weight: 400;
	font-style: normal;
}

@font-face {
	font-family: 'RobotoSlab';
