@font-face {
  font-family: 'asit';
  src: url('../fonts/asit.eot?60202316');
  src: url('../fonts/asit.eot?60202316#iefix') format('embedded-opentype'),
       url('../fonts/asit.woff2?60202316') format('woff2'),
       url('../fonts/asit.woff?60202316') format('woff'),
       url('../fonts/asit.ttf?60202316') format('truetype'),
       url('../fonts/asit.svg?60202316#asit') format('svg');
  font-weight: normal;
  font-style: normal;
}


h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  margin-top: $headings-margin-top;
}

blockquote {
	font-family:$font-family-serif;
	font-size: 1.7rem;
	color: gray;
	font-style:	italic;
	margin-left: $spacer;
}


span.meta {
	font-size: $font-size-sm*0.8;
	letter-spacing: 0.06rem;
	color: darken($gray,40%);
	text-transform: uppercase;

	a {
		color: darken($gray,40%) ;
		text-decoration: underline;
	}
}
h1 {
	margin-top: $spacer*1;
	margin-bottom: $spacer*1.2;
	font-size: 1.8rem;
	line-height: 1.2;
}

h2.entry-title a {
	color: $blue;
}

.content-container article header h1{
	margin-top:0;
}
@include media-breakpoint-up(md) {
	h1 {
		font-size: 2.2rem;
	}
}

.navigation.posts-navigation h2.screen-reader-text {
	display: none;
}

.partners-home h6 {
	letter-spacing: 0.1rem;
	text-transform: uppercase;
	color: darken($gray,30%);
	font-family:$font-family-sans-serif;
	margin-bottom: $spacer*4;
}

.block--bright h3 {
	color: #000;
}

// Font Sizes, see variables


// Icons
 [class^="icon-"]:before, [class*=" icon-"]:before, .dropdown-toggle:after, .navbar-toggler-icon, input.search-submit:after {
  font-family: "asit";
  font-style: normal;
  font-weight: normal;
  speak: none;

  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */

  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;

  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;

  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;

  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */

  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}

.icon-search:before { content: '\e800'; } /* '' */
.icon-menu:before { content: '\f0c9'; } /* '' */
.icon-angle-left:before { content: '\f104'; } /* '' */
.icon-angle-right:before { content: '\f105'; } /* '' */
.icon-angle-up:before { content: '\f106'; } /* '' */
.icon-angle-down:before { content: '\f107'; } /* '' */
