header.site-header {
	margin-top: 0;
	margin-bottom: 1.2rem;
	@include media-breakpoint-up(lg) {
		padding-top: $spacer*2;
		padding-bottom: $spacer*3;
	}
}

.home header.site-header {
	margin-bottom: 0;
}

header:after {
	content: "";
	display: table;
	clear: both;
}

section {
	padding-top: 100px;
	padding-bottom: 90px;

	@include media-breakpoint-up(xl) {
		padding-top: 130px;
		padding-bottom: 120px;
	}
}

/* Home */

section.header-home {
	padding-top: 0;
	padding-bottom: 0;
	h1 {
		margin-top: $spacer*1;
		margin-bottom: $spacer*1.2;
		font-size: 1.8rem;
		line-height: 1.2;
		font-weight: 300;
	}
	h3 {
		font-family: $font-family-sans-serif;
		font-size: $font-size-base;
		margin-bottom: 0.4rem;
	}
	@include media-breakpoint-up(lg) {
		h1 {
			margin-top: $spacer*1.3;
			margin-bottom: $spacer*1.8;
			font-size: 2.4rem;
		}
		h3 {
			margin-top: 0;
			margin-bottom: 0.2rem;
		}
	}
	@include media-breakpoint-up(xl) {
		h1 {
			font-size: 2.8rem;
		}
	}
}

.block-links-container {
	margin-top: $spacer;

	@include media-breakpoint-up(lg) {
		margin-top: 0;
		height: 460px;
		position: relative;
	}

	@include media-breakpoint-up(xl) {
		height: 490px;
	}
}

.news-home {
	min-height: 430px;

	@include media-breakpoint-up(xl) {
		min-height: 460px;
	}
	.btn {
		margin-top: $spacer;
	}
}
.content-container {
	margin-top: $spacer*3;
	margin-bottom: $spacer*5;
}

header {
	span.meta {
		margin-top: -10px;
		margin-bottom: 12px;
		display: block;
	}
}
header.entry-header {
	span.meta {
		margin-bottom: 22px;
	}
}

.logo-akkreditierung {
	width: 112px;
	float: right;
	margin-top: -60px;
	img {
		width: 100%;
		height: auto;
	}
}
footer {
	margin-top: $spacer*2;
}
