/*Buttons*/
.btn {
	text-transform: uppercase;
	letter-spacing: 0.1rem;
	font-size: $font-size-sm;
	border: 1px solid black;
}

.btn-sm {
	font-size: $font-size-sm*0.85;
}
.btn-default {
	color: #000;
}
.btn-default:hover {
	border: 1px solid black;
	color: $orange;
	border-color: $orange;
	background-color: transparent;
}
.btn-light:hover {
	border: 1px solid black;
	color: $blue-light;
	border-color: $blue-light;
	background-color: transparent;
}

.btn-light {
	border: 1px solid #fff;
	color: #fff;
	background-color: transparent;
	box-shadow: none;
}
